.pageup {
    @extend %flex-center;
    position: fixed;
    right: calc(46 * 100vw / 1920);
    bottom: 5rem;
    z-index: 100;
    width: 7rem;
    height: 7rem;
    border-radius: 50%;
    border: 0.1rem solid #505661;
    transition: 0.3s all;
    opacity: 0;
    pointer-events: none;

    @media (max-width: 1024px) {
        right: 1.6rem;
    }

    @media (max-width: 767px) {
        width: 5rem;
        height: 5rem;
    }

    &.active {
        opacity: 1;
        pointer-events: all;
    }

    &.in-footer {
        bottom: 14rem;

        @media (max-width: 767px) {
            bottom: 5rem;
        }
    }

    // .pageup__arrow

    &__arrow {
        width: 3rem;
        height: 3rem;

        @media (max-width: 767px) {
            width: 2rem;
            height: 2rem;
        }

        path {
            transition: 0.3s fill;
        }
    }

    @media (any-hover: hover) {
        &:hover {
            background-color: #505661;

            .pageup__arrow {
                path {
                    fill: #fff;
                }
            }
        }
    }
}
