html {
    box-sizing: border-box;
    font-size: calc(100vw / 1920 * 10);
    overflow-x: hidden;
    margin-right: calc(-1 * (100vw - 100%));

    @media (max-width: 1440px) {
        font-size: calc(100vw / 1440 * 10);
    }

    @media (max-width: 1024px) {
        font-size: calc(100vw / 768 * 10);
    }

    @media (max-width: 767px) {
        font-size: calc(100vw / 500 * 10);
    }

    @media (max-width: 500px) {
        font-size: calc(100vw / 360 * 10);
    }

    &.disable-fix {
        overflow-x: initial;
        margin-right: initial;
    }
}

body {
    min-width: 320px;
    font-family: 'Raleway', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #000;

    &._lock {
        overflow-y: hidden;
    }
}

button,
textarea,
input {
    font-family: 'Raleway', sans-serif;
    font-feature-settings: 'pnum' on, 'lnum' on;
}

dt {
    float: left;
}

.wrapper {
    position: relative;
    overflow-x: hidden;
    @extend %flex-col;
    min-height: 100vh;
}

.page {
    position: relative;
    flex: 1 0 auto; // Footer at bottom
    padding-bottom: 11rem;

    @media (max-width: 1024px) {
        padding-bottom: 0rem;
    }

    @media (max-width: 767px) {
        padding-top: 2.4rem;
    }

    @media (max-width: 500px) {
        padding-bottom: 3rem;
    }

    &__title {
        &:not(:last-child) {
            margin-bottom: 4rem;

            @media (max-width: 500px) {
                margin-bottom: 2rem;
            }
        }
    }

    &__subtitle {
        @extend %text-20;
        font-weight: 500;

        @media (max-width: 500px) {
            font-size: 1.4rem;
            line-height: 2.1rem;
        }

        &:not(:last-child) {
            margin-bottom: 5.2rem;

            @media (max-width: 500px) {
                margin-bottom: 2.5rem;
            }
        }
    }
}

.home-page {
    padding-top: 1.5rem;

    > * {
        &:first-child {
            padding-bottom: 7rem;
        }
    }

    &__seo-text {
        @media (min-width: 1025px) {
            max-width: 111.2rem;
        }
    }
}

.catalog {
    &__seo-text {
        @media (min-width: 1025px) {
            max-width: 82rem;
        }
    }
}

.player-btn {
    position: absolute;
    @extend %pos-center-xy;
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
    border: 0.1rem solid rgba(#fff, 0.7);
    transition: 0.3s background-color;

    &::before {
        content: '';
        position: absolute;
        @extend %pos-center-y;
        left: calc(50% - 2.1rem / 2);
        width: 2.1rem;
        height: 2.4rem;
        background-color: #fff;
        clip-path: polygon(0% 0%, 100% 50%, 0% 100%);
        transition: 0.3s background-color;
    }

    @media (any-hover: hover) {
        &:hover {
            background-color: #fff;

            &::before {
                background-color: #101112;
            }
        }
    }
}

.section {
    padding-bottom: 8rem;

    @media (max-width: 500px) {
        padding-bottom: 5rem;
    }

    &__title {
        @extend %flex-row-center;

        &:not(:last-child) {
            margin-bottom: 3rem;
        }
    }
}

.container {
    max-width: $container + $gap * 2;
    //max-width: calc(1500px + 1.6rem * 2);
    padding: 0 $gap;
    margin: 0 auto;
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: top;
    border: none;

    table & {
        max-width: none;
    }
}

img[data-src],
img[data-srcset] {
    display: block;
    min-height: 1px;
}

a {
    transition: 0.3s all;

    @media (any-hover: hover) {
        &:hover {
            //color: $green;
            color: #777;
        }
    }
}

.logo {
    &__link {
        @extend %vertical-align;
    }

    // .logo__img

    &__img {
        height: 3.1rem;
        object-fit: contain;
    }
}

.seo-text {
    p {
        @extend %text-16;

        @media (max-width: 500px) {
            font-size: 1.4rem;
            line-height: 2.1rem;
        }

        &:not(:last-child) {
            margin-bottom: 2rem;
        }
    }
}

.prices {
    display: flex;
    align-items: flex-end;
}

.new-price {
    @extend %text-16;
    color: $red;
    font-weight: 600;
}

.old-price {
    font-weight: 500;
    color: #9b9b9b;
    text-decoration: line-through;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);
}

table {
    width: 100%;
    text-align: center;
    overflow-x: auto;

    th {
        @extend %text-14;
        font-weight: 400;
        //color: #777;
        color: #9b9b9b;
        padding-top: 2.4rem;
        white-space: nowrap;
        padding: 2.4rem 1.25rem 0;
    }

    tbody {
        tr {
            &:nth-child(even) {
                background-color: #f4f4f6;
            }

            td {
                @extend %text-13;
                //color: #101112;
                padding: 2.1rem 0;

                b {
                    font-weight: 600;
                }
            }
        }
    }
}
